#bioSection {
    display: flex;
    flex-direction: column;
}

#bioText {
    order: 0;
    text-align: left;
    text-align: initial;
    font-size: 11vmin;
    padding: 0 12vmin;
}

#autor {
    order: 1;
    font-size: 4vmin;
    padding: 0 12vmin;
    text-align: right;
}
.albunsCapas {
    background-color: #bba34d;
    display: flex;
    align-items: center;
    justify-content: center;
}

.fp-section {
    text-align: center
}

.capas {
    width: 60vmin;
    height: 60vmin;
}

.fab {
    font-size: calc(15px + 6vmin);
    margin: 5vmin 5vmin 0 5vmin
}

.fab.fa-spotify:hover {
    color: #1e7d15
}

.fab.fa-youtube:hover {
    color: #dc1313
}

#container{
    margin: 0;
    padding: 0;
    position: relative;
    width: 100%;
    height: 100vh;
    display: flex;
    flex-direction: row wrap;
}

#map{
    height: 100%;
    width: 100%;
    float: right;
	-webkit-overflow-scrolling: auto;
}

#parteSemMapa {
    display: flex;
    height: 100%;
    width: 50vmin;
    float: left;
    flex-direction: column;
    align-items: center;
    justify-content: center;
}

#title{
    height: 5vmin;
}

#addMarcadorNoBancoBtn {
    display: flex;
    height: 6vmin;
    width: 90%;
    margin-bottom: 6vmin;
    justify-self: flex-end
}

@media (max-width: 584px) {
    #container{
        height: 100%;
    }
    
    #map{
        height: 100%;
        position: absolute !important;
        width: 100%;
        -webkit-overflow-scrolling: auto;
    }
    
    #parteSemMapa {
        display: flex;
        float: unset;
        flex-direction: unset;
        align-items: unset;
        justify-content: center;
        
        height: 100%;
        width: 100%;
        z-index: 1;
        bottom: 0;
        text-align: -webkit-center;
        
    }
    
    #title{
        width: 100%;
        height: 55px;
        z-index: 1;
        display: flex;
        justify-content: center;
        align-items: center;
        position: fixed;
        background-color: #3f51b5;
        color: #fff;
        font-family: "Roboto", "Helvetica", "Arial", sans-serif;
        font-weight: 500;
        font-size: 17px;
        text-transform: uppercase;
    }

    #divAddMarcadorNoBancoBtn{
        width: 100%;
        display: flex;
        justify-content: center;
    }

    #addMarcadorNoBancoBtn {
        width: 170px;
        position: fixed;
        bottom: 0;
        z-index: 1;
        font-size: 16px;
        border-radius: 26px;
    }
}

Dialog {
  display: 'flex';
  justify-content: 'center';
  flex-direction: column wrap;
  position: relative;
}

#divAvatar {
  width: 100%;
  text-align: -webkit-center;
  margin-top: 3vmin;
}

avatar {
  order: 0;
  margin: 10;
}

DialogTitle {
  order: 1;
}

.bigAvatar {
  width: 60px !important;
  height: 60px !important;
}
